@media only screen and (min-width:376px) and (max-width:1010px) {

    *{
        list-style: none;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        text-decoration: none;
    }
    
    /* Header------------------------------------------------------------------- */
    #header {
        position: relative;
        background-color: white;
        height: 60px;
        width: auto;
    
        margin: 0 auto 0 auto;
        padding: 0 0 0 0;
        max-width: 950px;
    
        padding: 0 auto 0 auto;
    
    }
    
    .header-menu{
        position: fixed;
        background-color: black;
        opacity: 0.8;
        height: 60px;
        width: 100%;
        z-index: 1;
        top: 0;
        left: 0;
    }
    
    .header1{
        height: 60px;
        width: 950px;
        margin: 0 auto 0 auto;    
        margin: 0 auto 0 auto;
        padding: 0 0 0 0;
        max-width: 950px;
    }
    
    .header1 .logo {
        position: fixed;
        top: 20px;
        left: 25px;
        z-index: 1;
        color: white;
        font-size: 35px;
        font-weight: bold;
    }
    
    .menu {
        position: fixed;
        top: 32px;
        left: 90%;
        z-index: 1;
        font-size: 20px;
    }
    
    .menu a {
        padding: 0 10px;
        color: black;
    }
    
    .menu a:first-child {
        display: none;
    }
    
    #nav-drawer {
        position: relative;
    }
    
/* Hamburger------------------------------------------------------- */
    .nav-unshown {
        display:none;
    }
    
    #nav-open {
        position: absolute;
        display: block;
        cursor: pointer;
        top: -20px;
        color: white;
    }
    
    #nav-close {
        display: none;
        position: fixed;
        z-index: 99;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: black;
        opacity: 0;
        transition: .3s ease-in-out;
    }
    
    /*中身*/
    #nav-content {
        overflow: auto;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9999;
        width: 90%;
        max-width: 330px;
        height: 100%;
        background: #fff;
        transition: .3s ease-in-out;
        -webkit-transform: translateX(-105%);
        transform: translateX(-105%);
    }
    
    #nav-input:checked ~ #nav-close {
        display: block;
        opacity: .5;
    }
    
    #nav-input:checked ~ #nav-content {
        -webkit-transform: translateX(0%);
        transform: translateX(0%);
        box-shadow: 6px 0 25px rgba(0,0,0,.15);
    }
    
    .menu a:not(:first-child) {
        display: none;
    }
    
    .menu a:first-child {
        display: block;
    }
    
    /* Home------------------------------------------------------------------- */
