    }
    
    .down-arrow{
        position: absolute;
        left: 0;
        right: 0;
        text-align: center;
    }
    
    .down-arrow a {
        width: 50px;
        height: 45px;
        background: #ffffff;
        border-radius: 0 0 10px 10px;
        display: inline-block;
        vertical-align: top;
        box-shadow: 0px 11px 12px 0px rgba(0, 0, 0, 0.16);
        cursor: pointer;
    }
    
    .down-arrow a:after {
        content: "";
        position: absolute;
        top: -5px;
        left: 0;
        right: 0;
        bottom: 0;
        border-bottom: 3px solid #181b31;
        border-right: 3px solid #181b31;
        width: 15px;
        height: 15px;
        margin: auto;
        -moz-transform: rotate(45deg);
        -o-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        transform: rotate(45deg);
    }
    
